.projectsSlides {
    position: absolute;
    top: 20%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #2d2d2d;
    height: 60%;
    padding-bottom: 20px;
    width: 100%;
    z-index: 10000;
    align-items: center;
    -webkit-animation: slideup 2s backwards;
            animation: slideup 2s backwards;
    box-shadow: 
    2px 2px 5px black;
    text-align: center;
    -webkit-filter: brightness(3);
            filter: brightness(3);
}

.projectsLayer {
    position: absolute;
    top:0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.8);
    z-index: 1000;
    cursor: pointer;
}

.imgFrame {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    padding: 0;
}

.projectsSlides img {
    width: 300px;
    height: 300px;
    margin: 0px;
    -webkit-transition: 1s -webkit-transform;
    transition: 1s -webkit-transform;
    transition: 1s transform;
    transition: 1s transform, 1s -webkit-transform;
    object-fit: cover;
    -webkit-filter: brightness(0.3);
            filter: brightness(0.3);
}

.projectsSlides .textover {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    cursor: pointer;
    display: none;
    text-align: center;
    color: #C9AD98;
    cursor: pointer;
    padding: 20px;
    font-size: 18px;
}

.imgFrame:hover .textover {
    display: block;
}

.imgFrame:hover .fa-external-link-alt {
    font-size: 40px;
    margin-top: 50px;
}

.imgFrame:hover img {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-filter: brightness(0.1);
            filter: brightness(0.1);
}

.first {
    -webkit-animation: zoomInnOut 1s 1.5s backwards;
            animation: zoomInnOut 1s 1.5s backwards;
}

.second {
    -webkit-animation: zoomInnOut 1s 2s backwards;
            animation: zoomInnOut 1s 2s backwards;
}

.third {
    
    -webkit-animation: zoomInnOut 1s 2.5s backwards;
    
            animation: zoomInnOut 1s 2.5s backwards;
}

.fa-caret-left, .fa-caret-right {
