.aboutMe {
    background-color: rgba(245,245,220,0.7);
    position: absolute;
    top: 10%;
    left: 340px;
    border-radius: 0 50px 50px 0;
    width: 100px;
    cursor: pointer;
    overflow: hidden;
    display: none;
}

.info {
    display: none;
    height: 100%;
}

@keyframes extend {
    from  {
        width: 100px;
    }
    to {
        width: 300px;
    }
}
@keyframes shrink {
    from  {
        width: 300px;
    }
    to {
        width: 100px;
    }
}
@keyframes fadeIn {
    from  {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeOut {
    from  {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}