.projectsSlides {
    position: absolute;
    top: 20%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #2d2d2d;
    height: 60%;
    padding-bottom: 20px;
    width: 100%;
    z-index: 10000;
    align-items: center;
    animation: slideup 2s backwards;
    box-shadow: 
    2px 2px 5px black;
    text-align: center;
    filter: brightness(3);
}

.projectsLayer {
    position: absolute;
    top:0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.8);
    z-index: 1000;
    cursor: pointer;
}

.imgFrame {
    width: fit-content;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    padding: 0;
}

.projectsSlides img {
    width: 300px;
    height: 300px;
    margin: 0px;
    transition: 1s transform;
    object-fit: cover;
    filter: brightness(0.3);
}

.projectsSlides .textover {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    display: none;
    text-align: center;
    color: #C9AD98;
    cursor: pointer;
    padding: 20px;
    font-size: 18px;
}

.imgFrame:hover .textover {
    display: block;
}

.imgFrame:hover .fa-external-link-alt {
    font-size: 40px;
    margin-top: 50px;
}

.imgFrame:hover img {
    transform: scale(1.5);
    filter: brightness(0.1);
}

.first {
    animation: zoomInnOut 1s 1.5s backwards;
}

.second {
    animation: zoomInnOut 1s 2s backwards;
}

.third {
    
    animation: zoomInnOut 1s 2.5s backwards;
}

.fa-caret-left, .fa-caret-right {
    font-size: 60px;
    cursor: pointer;
    margin: 20px;
    color: #202020;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
}

.fa-caret-left:hover, .fa-caret-right:hover {
    transform: scale(1.1);
}

h2 {
    flex: 0 0 100%;
    font-family: 'Titillium Web', sans-serif;
    letter-spacing: 7px;
    text-transform: uppercase;
    width: 100%;
    color: #202020;
    letter-spacing: .1em;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
      margin: 0;
      padding: 0;

}

p {
    
    font-family: 'Titillium Web', sans-serif;
}

@keyframes slideup {
    from {top: 200px; opacity: 0} 
    to {top: 20%; opacity: 1}
}


@keyframes zoomInnOut {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}
